@include option("blockquote") {

[class*="#{class("Blockquote")}"],
[class*="#{class("Auto-formating-block")}"] blockquote {
	padding: $blockquote-padding;
	background: var(--blockquote-background);
    color: var(--blockquote-color);
	@include border-radius($blockquote-border-radius);
	position: relative;
    margin-top: $blockquote-margin-top;
    margin-bottom: $blockquote-margin-bottom;

    @include option("blockquote-quote") {
        padding-left: 80px;

    	&:before {
    		content: "\201C";
    		position: absolute;
    		left: 15px;
    		top: 15px;
    		font-family: georgia, serif;
    		font-size: 7em;
    		line-height: .9;
    		color: var(--blockquote-quote-color);
    	}
    }

	> :last-child {
		margin-bottom: 0;
	}

	> cite {
		display: block;
		color: var(--blockquote-cite-color);
		text-align: $blockquote-cite-text-align;
        margin-bottom: $blockquote-cite-margin-bottom;

		&:before {
			content: "- ";
		}
	}

    @include option("components-inverted") {
    	&[class*="#{class("(Component)-inverted")}"] {
            background: var(--blockquote-background-inverted);
            color: var(--blockquote-color-inverted);

            &:before {
    		    color: var(--blockquote-quote-color-inverted);
            }

            > cite {
    		    color: var(--blockquote-cite-color-inverted);
            }
        }
    }

    @include option("components-light") {
    	&[class*="#{class("(Component)-inverted")}"] {
            background: $blockquote-background;
            color: $blockquote-color;

            &:before {
    		    color: $blockquote-quote-color;
            }

            > cite {
    		    color: $blockquote-cite-color;
            }
        }
    }

    @include option("components-dark") {
    	&[class*="#{class("(Component)-dark")}"] {
            background: $blockquote-background-dark;
            color: $blockquote-color-dark;

            &:before {
    		    color: $blockquote-quote-color-dark;
            }

            > cite {
    		    color: $blockquote-cite-color-dark;
            }
        }
    }
}

}
